import { useState } from "react";
import "../../css/footer.css";
import CustomButton from "../CustomButton";
import logo from "../../images/starbucks_logo_only_text.png"

const Footer = () => {

  const [year] = useState(2024);

  return (
    <footer>
      <div className="inner">
        <ul className="menu">
          <li>
            <a className="green" href="#none"></a>
          </li>
          <li>
            <a href="#none">  </a>
          </li>
          <li>
            <a href="#none"> </a>
          </li>
          <li>
            <a href="#none"> </a>
          </li>
          <li>
            <a href="#none">  </a>
          </li>
          <li>
            <a href="#none"> </a>
          </li>
        </ul>
        <div className="btn-group">
          <CustomButton colorType="white"> </CustomButton>
          <CustomButton colorType="white"></CustomButton>
          <CustomButton colorType="white"> </CustomButton>
        </div>
        <div className="info">
          <span>   201-81-21515</span>
          <span>()   </span>
          <span>TEL : 02) 3015-1100 / FAX : 02) 3015-1106</span>
          <span>  : </span>
        </div>
        <div className="copyright">
          <p>&copy; <span className="this-year">{year}</span> Starbucks Coffee Company. All Rights Reserved.</p>
          <img className="logo" src={logo} alt="LOGO"/>
        </div>
      </div>
    </footer>
  );
};

export default Footer;