import CustomButton from "../CustomButton";

const RewardsSection = () => {
  return (
    <section className="rewards">
      <div className="bg-left"></div>
      <div className="bg-right"></div>
      <div className="inner">
        <div className="btn-group">
          <CustomButton colorType="reverse" classList={["sign-up"]}></CustomButton>
          <CustomButton classList={["sign-in"]}></CustomButton>
          <CustomButton classList={["gift"]}>e-Gift </CustomButton>
        </div>
      </div>
    </section>
  );
};

export default RewardsSection;